// 引入post请求
import {
	publicing
} from '../../../api/api.js'
// 引入请求地址
import {
	startingurl,
	multipleurl
} from '../../../api/request.js'
export default {
	data() {
		return {
			flag: false,
			synthesize: "综合排序",
			drop: false,
			num: 0,
			sortmen: false,
			ying: false,
			scrnum: -1,
			// 接收筛选的对象
			multiobj: {},
			// 人均价筛选
			person: [{
				"title": "人均价",
				"datas": [{
						"name": "20元以下",
						"per": {
							"$lt": 20
						},
					},
					{
						"name": "20-40元",
						"per": {
							"$lte": 40,
							"$gte": 20
						},
					},
					{
						"name": "40元以上",
						"per": {
							"$gt": 40
						},
					},
				]
			}],
			// 多选
			screendata: [{
				"title": "商家特色(可多选)",
				"datas": [{
						"id": 1,
						"sign": 'duration',
						"name": '配送最快'
					},
					{
						"id": 1,
						"sign": 'deliver',
						"name": '0元起送'
					},
					{
						"id": 1,
						"sign": 'physi',
						"name": '免配送费'
					}
				],
			}],
			sortlist: [{
					"name": "综合排序",
					"screen": "_id",
					"nums": 1
				},
				{
					"name": "起送价最低",
					"screen": "delivering",
					"nums": 1
				},
				{
					"name": "配送费最低",
					"screen": "physical",
					"nums": 1
				},
				{
					"name": "人均高到低",
					"screen": "capita",
					"nums": -1
				},
				{
					"name": "人均低到高",
					"screen": "capita",
					"nums": 1
				}
			],
		}
	},
	methods: {
		// 综合排序
		multiple() {
			this.drop = true
			this.sortmen = false
			this.backOne()
		},
		sortClick(index, name, screen, nums) {
			this.synthesize = name
			this.num = index
			this.backClic()
			this.starTing(screen, nums)
		},
		// 透明背景
		backOne() {
			this.ying = true
		},
		// 隐藏透明背景
		backClic() {
			this.ying = false
			this.drop = false
			this.sortmen = false
		},
		// 销量，速度，津贴
		saLes() {
			this.backClic()
		},
		// 筛选
		scReen() {
			this.drop = false
			this.sortmen = true
			this.backOne()
		},
		// 综合排序的请求
		starTing(screen, nums) {
			let Data = {
				screen,
				nums
			}
			publicing(startingurl, Data)
				.then((res) => {
					// 存储到vuex数据仓库
					this.$store.dispatch('screenmutaAction', res.data)
				})
				.catch((err) => {
					console.log(err)
				})
		},
		// 商家特色（多选）
		screenchoIce(indexs, sign) {
			if (this.screendata[0].datas[indexs].id == 1) {
				// 选中
				this.screendata[0].datas[indexs].id = 2
				// this.$set给对象添加一个属性
				this.$set(this.multiobj, sign, sign)
			} else {
				// 未选中
				this.screendata[0].datas[indexs].id = 1
				this.$delete(this.multiobj, sign)
			}
		},
		// 人均价（单选）
		personchoIce(index, name) {
			if (this.scrnum == index) {
				// 未选中
				this.scrnum = -1
				//后台接口文档中的capita
				this.$delete(this.multiobj, 'capita')
			} else {
				// 选中
				this.scrnum = index
				this.$set(this.multiobj, 'capita', name)
			}
		},
		// 清空
		emPty() {
			// 商家特色
			this.screendata.forEach((item) => {
				let arr = item.datas.map((items) => {
					items.id = 1
					return items
				})
			})
			// this.multiobj = {}
			// 人均价
			this.scrnum = -1
			this.backClic()
		},
		// 完成
		compLete() {
			this.backClic()
			// 作业：完成没有商品数据的提示
			publicing(multipleurl, this.multiobj)
				.then((res) => {
					console.log(res)
					// 存储到vuex数据仓库
					this.$store.dispatch('screenmutaAction', res.data)
				})
				.catch((err) => {
					console.log(err)
				})
		},

	},
	// 如果用户没有点击任何筛选条件，完成按钮应该被禁用
	computed: {
		// this.multiobj如果是空对象，禁止点击完成按钮，反之。。。。
		emptyIng() {
			let obj = JSON.stringify(this.multiobj) == '{}'
			console.log(obj) //true:空对象
			if (obj == true) {
				this.flag = false
			} else {
				this.flag = true
			}
		}
	}
}
